*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.container{
  max-width: 900px;
  margin: 0 auto;
}

header{
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
  align-items: center;
}

header .site_logo{
  width: 96px;
  cursor: pointer;
}

body{
  background: #e4e9e4;
}


.ymaps-2-1-79-gototech, .ymaps-2-1-79-gotoymaps, .ymaps-2-1-79-copyright__content{
  display: none!important;
}

.ymaps-2-1-79-balloon__content ymaps{
  padding: 10px 0!important;
  min-height: 210px!important;
  overflow: hidden;
}

.ymaps-2-1-79-balloon, .ymaps-2-1-79-balloon__content{
  background: #e4e9e4!important;
}


.scooter_main{
  display: flex;
}

.scooter_main section{
  width: 90%;
}

.scooter_header{
  display: flex;
}


aside img{
  width: 120px;
  padding-top: 15%;
}


.scooter_header{
  padding: 10px 20px;
  font-size: 17px;
  border-radius: 8px;
  border: 1px solid #00000033;
  background: #e1e1e1;
  text-align: center;
  justify-content: center;
}

.scooter_body{
  display: flex;
  justify-content: space-between;
}

.scooter_battery_power{
  border: 1px solid #e7e4e4;
  border-radius: 7px;
  padding: 1px 24px;
}

.battery_power_low{
  background: #c3c564;
  color: #fff;
}

.battery_power_good{
  background: #52b732;
  color: #fff;
}

.scooter_body article{
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin: 10px 30% 0 1%;
  align-items: center;
}

.scooter_body article div p{
  margin: 0px;
}


.scooter_body article div img{
  width: 20px;
}

.scooter_body article div .scooter_info{
  display: flex;
  font-weight: bold;
  border: 2px solid #cfcf73;
  padding: 3px 20px 3px 10px;
  border-radius: 14px;
  width: fit-content;
}

.scooter_body article div .scooter_info p{
  margin-left: 8px;
}


.scooter_body article .scooter_title{
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 7px;
}


.scooter_footer{
  display: flex;
  justify-content: space-around;
}

.br{
  width: 100%;
  background: black;
  height: 1px;
  margin: 7px 0;
}

.adress{
  text-align: center;
  font-stretch: condensed;
  color: #3e6851;
}

.scooter_footer button{
  padding: 13px 34px;
  border-radius: 30px;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
}

.scooter_footer button span{
  display: block;
  font-size: 11px;
  font-weight: 700;
}

.scooter_footer button:first-child{background: #9fe7a1;}
.scooter_footer button:last-child{background: #39eb3e;}

footer{
  background: black;
  color: white;
  padding: 20px 0;
}


.model_scooter{
  position: absolute;
  background: #e4e9e4;
  bottom: 0;
  width: 100%;
  background: #e4e9e4;
  padding: 10px 0;
}

main{
  position: relative;
}